.main {
    .header {
        background-color: #3368c6;
        width: 100%;
        z-index: 10;
        height: 50px;
        line-height: 50px;
        position: relative;

        .container {
            .row {
                .col-lg-12 {
                    .left {
                        display: flex;
                        color: white;
                        font-size: 14px;

                        .number {
                            display: flex !important;
                            margin-right: 30px;
                            position: relative;

                            .icon {
                                margin-right: 8px;

                                i {}
                            }

                            &::after {
                                content: "";
                                height: 21px;
                                width: 1px;
                                display: block;
                                position: absolute;
                                left: 129px;
                                top: 16px;
                                background-color: rgba(255, 255, 255, .08);
                            }
                        }

                        .mail {
                            display: flex;

                            .icon {
                                margin-right: 8px;

                                i {}
                            }

                            a {
                                color: white !important;
                                /* list-style: none; */
                                text-decoration: none;
                            }
                        }
                    }
                }

                font-size: 14px;
                display: flex;

                .work-period {
                    display: flex;
                    margin-right: 30px;
                    color: white;
                    margin-left: auto;
                    position: relative;

                    .icon {
                        i {
                            margin-right: 8px;
                        }
                    }

                    &::after {
                        content: "";
                        height: 21px;
                        width: 1px;
                        display: block;
                        position: absolute;
                        /* left: 210px; */
                        right: -27px;
                        top: 16px;
                        background-color: rgba(255, 255, 255, .08);
                    }
                }

                .social {
                    .social-ul {
                        display: flex;
                        list-style: none;

                        li {
                            min-width: 20px;
                            margin-left: 0;
                            font-size: 14px;

                            .tooltip-fb {

                                position: relative;
                                display: inline-block;
                                border-bottom: 1px dotted black;
                                
                                a {
                                    color: white !important;

                                    i {

                                    }
                                }

                                .tooltiptext {
                                    color: #fff;
                                    text-align: center;
                                    border-radius: 6px;
                                    padding: 5px 0;
                                    width: 120px;
                                    background-color: black;
                                    visibility: visible;

                                }
                               
                                
                            }
                           

                           


                        }
                    }
                }

            }
        }
    }

    .navbar {
        .container {
            .row {
                .col-lg-12 {
                    .main-menu {
                        line-height: 95px;
                        display: flex;

                        .logo {
                            margin-right: 9pc;
                            // margin-right: auto !important;
                            display: flex;

                            a {
                                img {}
                            }
                        }

                        .menu {
                            ul {
                                display: flex;

                                //                             padding: 0px;
                                // margin: 0px;
                                li {
                                    list-style: none;

                                    a {
                                        padding: 46px 17px;
                                        text-decoration: none;
                                        color: black !important;
                                    }
                                }
                            }
                        }

                        .extra {    
                            display: flex;

                            .appointment {
                                a {
                                    display: block;
                                    padding: 0px 17px 10px 15px;
                                    text-decoration: none;
                                    color: black !important;

                                    i {}
                                }

                                .addition-appointment {
                                    h3 {}

                                    form {}
                                }
                            }

                            .search {

                                a {
                                    text-decoration: none;

                                    i {
                                        color: red;
                                    }
                                }

                                .addition-search {
                                    form {}
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

